import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AlertController, IonItemSliding, Platform } from '@ionic/angular';
import { Driver, Vehicle } from 'src/Table/table';
import { DriverService } from '../Service/driver.service';
import { Plugins, CameraSource, CameraResultType } from '@capacitor/core';
import { VehicleService } from '../Service/vehicle.service';
@Component({
  selector: 'app-driver',
  templateUrl: './driver.page.html',
  styleUrls: ['./driver.page.scss'],
})
export class DriverPage implements OnInit {
  @ViewChild('filePicker', { static: false }) filePickerRef: ElementRef<HTMLInputElement>;
  usePicker = false;
  regform = this.fb.group({});
  driverId: string;
  listOfDriver: any[]=[];
  editMode: boolean;
  base64textString: string;
  listOfVehicle: Vehicle[];
  defaultVehicleId: string;
  constructor(private fb: FormBuilder,
    private driverService: DriverService,
    private alertController: AlertController,
    private vehicleService:VehicleService,
    private platform:Platform) { }

  ngOnInit() {
    this.regform = this.fb.group({
      FullName: ["", Validators.required],
      Address: ["",Validators.required],
      PhoneNumber: ["",Validators.required],
      Vehicle: [""],//Restaurant
      Photo: [""],
      InitialLocationofDay: [""],
    });
    if ((this.platform.is('mobile') && !this.platform.is('hybrid')) ||
    this.platform.is('desktop')
  ) {
    this.usePicker = true;
  }
  this.getVehicle();
  this.getDriver();
  }
  getDriver() {
    this.driverService.getAllDriver().subscribe(res => {
      this.listOfDriver=[];
      res.forEach(ele=>{
          let data={
            id:ele.id,
            FullName:ele.FullName,
            Address:ele.Address,
            PhoneNumber:ele.PhoneNumber,
            Vehicle:this.listOfVehicle.find(c=>c.id==ele.Vehicle).MotorNumber,//Restaurant
            Photo:ele.Photo,
            InitialLocationofDay:ele.InitialLocationofDay
          }
          this.listOfDriver.push(data);
       })
     })
  }
  getVehicle(){
    this.vehicleService.getAllVehicle().subscribe(res=>{
      this.listOfVehicle = res;
      this.defaultVehicleId = res[0].id
    })
  }
  saveDriver() {
    if (this.base64textString !== undefined) {
      this.regform.get('Photo').setValue(this.base64textString);
    }
    if (this.regform.valid) {
      if (!this.driverId) {
        this.driverService.create(this.regform.value);
      }
      else {
        this.driverService.updateDriver(this.regform.value, this.driverId)
      }
      this.base64textString = '';
      this.driverId = null;
      this.regform.reset();
      this.presentAlert();
    }
    else {
      this.ErrorAlert();
    }
  }
  edit(driver: Driver) {
    this.editMode = true;
    this.driverId = driver.id;
    this.regform.get('FullName').setValue(driver.FullName);
    this.regform.get('Address').setValue(driver.Address);
    this.regform.get('PhoneNumber').setValue(driver.PhoneNumber);
    this.regform.get('Vehicle').setValue(this.listOfVehicle.find(c=>c.MotorNumber==+driver.Vehicle).id);
    this.regform.get('InitialLocationofDay').setValue(driver.InitialLocationofDay);
    this.base64textString = driver.Photo;
   
  }
  delete(driver: Driver) {
    this.presentAlertConfirm(driver);
  }
  async presentAlertConfirm(driver: Driver) {
    const alert = await this.alertController.create({
      header: 'Confirm!',
      message: '<strong>Are you sure you want delete?</strong>',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'OK',
          handler: () => {
            this.driverService.removeDriver(driver.id);
            this.regform.reset();
            this.driverId = null;
          }
        }
      ]
    });
    await alert.present();
  }
  async ErrorAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Error',
      // subHeader: 'Subtitle',
      message: 'Please Enter All field.',
      buttons: ['OK']
    });

    await alert.present();
  }
  async presentAlert() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Driver ',
      // subHeader: 'Subtitle',
      message: 'Driver Saved successfully.',
      buttons: ['OK']
    });

    await alert.present();
  }
  onPickImage() {
    if (this.usePicker == true) {
      // if (!Capacitor.isPluginAvailable('Camera')) {
      this.filePickerRef.nativeElement.click();
      return;
    }
    Plugins.Camera.getPhoto({
      quality: 50,
      source: CameraSource.Prompt,
      correctOrientation: true,
      height: 100,
      width: 100,
      resultType: CameraResultType.Base64
    })
      .then(image => {
        this.base64textString = 'data:image/png;base64,' + image.base64String;
      })
      .catch(error => {
        console.log(error);
        if (this.usePicker) {
          this.filePickerRef.nativeElement.click();
        }
        return false;
      });
  }
  onFileChosen(event: Event) {
    const pickedFile = (event.target as HTMLInputElement).files[0];
    if (!pickedFile) {
      return;
    }
    const fr = new FileReader();
    fr.onload = () => {
      const dataUrl = fr.result.toString();
      this.base64textString = dataUrl;
    };
    fr.readAsDataURL(pickedFile);
  }
  
}
